.contact__holder {
  width: 100%;
  font-style: normal;
  letter-spacing: 2px; }

.contact__block {
  margin-bottom: 1.5rem; }

.contact__phone-number {
  margin-bottom: 0.375rem; }

.contact__link {
  color: #fff;
  text-decoration: none;
  display: inline-block;
  padding-bottom: 1px;
  margin-bottom: 0.375rem;
  position: relative; }
  .contact__link:after {
    display: inline-block;
    content: '';
    background-color: #fff;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 1px;
    -webkit-transition: width 0.3s ease-in-out;
    -o-transition: width 0.3s ease-in-out;
    transition: width 0.3s ease-in-out; }
  .contact__link:hover {
    background-image: -webkit-gradient(linear, left top, right top, color-stop(0, #f2f), color-stop(1, #2ff));
    background-image: gradient(linear, left top, right top, color-stop(0, #f2f), color-stop(1, #2ff));
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out; }
    .contact__link:hover:after {
      width: 100%;
      -webkit-transition: width 0.3s ease-in-out;
      -o-transition: width 0.3s ease-in-out;
      transition: width 0.3s ease-in-out; }



